.Login {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  color: #efe3c8;
  background: #201520;
  overflow: hidden;
}

.Eye {
  height: 20px;
  margin-right: 2%;
}

.ShowPass {
  display: flex;
  justify-self: flex-end;
  align-items: center;
  margin-top: 3%;
  margin-left: 210px;
  font-size: 12px;
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
  color: #efe3c8;
  width: 123px;
}

.SignIn {
  padding: 1px;
  height: 50%;
  background-color: transparent;
  border-color: transparent;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.UserLogin,
.PasswordLogin {
  display: flex;
  align-items: center;
  padding: 10px 10px 10px 10px;
  /* position: fixed; */
  width: 312px;
  height: 20px;
  /* align-self: center; /* left: 16px; */
  background: #171017;
  border-radius: 10px;
  border: 1px solid #000000;
  color: #efe3c8;
}

.UserLogin:focus,
.PasswordLogin:focus {
  outline: none;
  box-shadow: 0 0 5px #719ece;
}

.SignInBtn {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  justify-self: flex-end;
  padding: 15px 50px;
  margin-top: 10px;

  width: 343px;
  height: 45px;

  cursor: auto;
  background: #efe3c8;
  box-shadow: 2px 2px 15px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
}

.SignInBtn:hover {
  background-color: tomato;
}

.Links {
  font-size: 12px;
  width: 325px;
  display: flex;
  justify-content: space-between;
}

a {
  text-decoration-line: none;
  color: #efe3c8;
}

.PassShow {
  font-size: 12px;
}

.loggedIn {
  background-color: rgba(93, 185, 93, 0.637);
  color: aliceblue;
  font-size: 20px;
  border: 1px solid rgb(245, 238, 238);
  width: 550px;
  height: 10px;
  padding: 5px 30px 5px 30px;
}

.userNotExist {
  background-color: rgb(247, 8, 8);
  color: aliceblue;
  font-size: 20px;
  border: 1px solid rgb(245, 238, 238);
  width: 550px;
  height: 10px;
  padding: 5px 30px 5px 30px;
}
